.img img {
  width: 100%;
}

.img {
  height: 180px;
  overflow: hidden;
  align-items: center;
  border-radius: 10px;
}


.homeimg img {
  width: 100%;
}

.home_container {

  margin-top: 120px;
}
.latest_container {
  background: #5252521f;
  padding: 3% 5%;
  box-shadow: rgb(60 64 67 / 30%) 0px 1px 2px 0px,
    rgb(60 64 67 / 15%) 0px 1px 3px 1px;
}

.latest_container a {
  text-decoration: none;
  color: var(--text-color);
}

.latestimg_div {
  margin: 3%;
}

.latestimg_div:hover {
  border: .1px solid var(--text-color);
  border-radius: 10px;
}
.latest_description {
  padding: 2%;
}

.latest_description h3 {
  display: block;
  /* margin: 0 0 4px 0; */
  font-weight: 500;
  max-height: 4rem;
  display: -webkit-box;
  overflow: hidden;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  text-overflow: ellipsis;
  white-space: normal;
  font-size: 15px;
  color: var(--titles);
}


/* New syles */

.homecontent_container {
    margin: 0 5%;
}

.thinkers_container {
    display: flex;
    flex-wrap: wrap;
}

.thinkers_container .content_box {
    width: 14.6%;
    margin: 1%;
    /* height: 100%; */
}


.thinkers_container .contentbox_img {
    max-height: 160px;
    overflow: hidden;
}


/* Home Categories */

.home-content_container {
  margin: 140px 3% 0;
  display: block;
  column-count: 6;
  column-gap: 2%;
}

.home-content_container .content_box {
  overflow: auto;
  margin: auto;
  margin-bottom: 6%;
}


@media screen and (max-width: 1800px) {
    .thinkers_container .contentbox_img {
        height: 130px;
    }
    .img {
        height: 150px;
      }

      .thinkers_container .content_box {
        width: 14.5%;
      }

      .home-content_container {
        column-count: 5;
      }
  }


@media screen and (max-width: 1440px) {
    .thinkers_container .contentbox_img {
        height: 140px;
    }

    .thinkers_container .content_box {
        width: 23%;
    }
    .img {
        height: 100px;
      }
      .home-content_container {
        column-count: 4;
      }
  }

  @media screen and (max-width: 1100px) {
    .thinkers_container .contentbox_img {
        height: 110px;
    }

  }

  @media screen and (max-width: 1024px) {
    .img {
      height: 90px;
    }
  }

  @media screen and (max-width: 768px) {
    .thinkers_container .contentbox_img {
        height: 100px;
    }

    .thinkers_container .content_box {
        width: 31.3%;
    }

    .homecontent_container {
        margin: 0 2%;
    }
    .home-content_container {
      column-count: 3;
    }
    .img {
      height: 70px;
    }
  }

  
  @media screen and (max-width: 600px) {
    .thinkers_container .contentbox_img {
        height: 120px;
    }

    .thinkers_container .content_box {
        width: 48%;
    }
    .homecontent_container {
        margin: 0 1%;
    }

    .latest_container .slick-next {
        right: 5px;
    }

    .latest_container .slick-prev {
        left: 4px;
        z-index: 1;
    }
    .home-content_container {
      column-count: 2;
    }
  }

  
  
  @media screen and (max-width: 425px) {
    .thinkers_container .content_box .contentbox_img {
        height: 100%;
        overflow: unset;
        max-height: unset;
    }

    .thinkers_container .content_box {
        width: unset;
        margin-bottom: 4%;
    }

    .thinkers_container {
      display: block;
      column-count: 1;
      margin-left: 3%;
      margin-right: 3%;
    }
  }

  @media screen and (max-width: 500px) {
    .home-content_container {
      margin-top: 120px;
    }
  }

  @media screen and (max-width: 425px) {
    .thinkers_container .contentbox_img {
        height: 170px;
    }
    .homecontent_container {
        margin: 0;
    }
    .home-content_container {
      column-count: 1;
    }

  }

  

.latest_container .slick-next::before,
.latest_container .slick-prev::before {
  color: var(--text-color);
  font-size: 25px;
}
